.header {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 60px;
    background-color: #f8f9fa;
    padding: 10px 20px;
    background-color: #343b45;
}

.logoBtn {
    position: absolute;
    background-color: inherit;
    border-radius: 5px;
    border: none;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
}

.logoBtn:hover {
    background-color: rgba(111, 99, 99, 0.561);

}

.logoBtn:active {
    background-color: rgb(111, 99, 99);
}

.inform {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    gap: 10px;
    color: white;
}

.movieList {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
}

.mobileHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 60px;
    background-color: #f8f9fa;
    padding: 2px 5px;
    background-color: #433445;
}

.mobileHeader .inform {
    top: 50%;
    transform: none;
}

.loginBtn {
    background-color: inherit;
    border-radius: 5px;
    border: none;
    transform: translateY(-50%);
    cursor: pointer;
}